/* trying to make it responsive */

.footer {
  width: 100%;
  height: 70%;
  background-color: #0a0453;
  display: flex;
  justify-content: space-evenly;
  text-align: start;
  bottom: 0;
}

@media (max-width: 850px) {
  .footer{
      display: flex;
      bottom: 0;
      padding: auto;
      /* position: relative; */
      justify-content: space-evenly;
  }

}

.icons {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.home h1,h2,h3,h4,h5,h6,p{
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif
}
.mid-home{
    background-color: gray;
    padding:20px;
    /* width: 100%; */
}
.buttons-container{
    display: grid;
    grid-template-columns:  repeat(3, 1fr);
    grid-gap: 10px;
    gap: 10px;
}


@media (max-width: 850px) {
    .buttons-container{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        font-size: 5px;
        height: 100px;
        width: 150px;
    }
    .container{
        display: flex;
        height: auto;
        width: auto;
    }
    .contentComputer{
        display: flex;
        bottom: 0;
    }
    .container img{
        width:100px;
        height:100px;
        margin-left: 25px;
        margin-top: 20px;
    }
}


@media (max-width: 540px) {
    .buttons-container{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        height: 100px;
        width: 200px;
    }
    .container{
        display: flex;
        height: 100vh;
        width: 100%;
    }
    .contentComputer{
        display: flex;
        bottom: 0;
    }
    .container img{
        width:100px;
        height:100px;
        margin-left: 20px;
        margin-top: 10px;
    }
}

